import { Component, OnInit, Input } from '@angular/core';
import {CarInfoModel} from '../model/carinfo.model';
import {MainService} from '../main.service';
import { ActivatedRoute, Router } from '@angular/router';
import { sha256 } from 'js-sha256';
import {environment} from '../../environments/environment';
import * as VinValidator from 'vin-validator';
@Component({
  selector: 'app-search-page',
  templateUrl: './search-page.component.html',
  styleUrls: ['./search-page.component.css']
})
export class SearchPageComponent implements OnInit {
  vin_number: string;
  searched_number:string;
  is_validvin:boolean;
  isLoading:boolean;
  carInfo: CarInfoModel;  

  focus;
  constructor(private mainService: MainService, private route: ActivatedRoute, private router: Router) {  
    this.isLoading = true;
    this.carInfo = new CarInfoModel();    
    
  }

  ngOnInit(): void {
    this.vin_number = this.route.snapshot.paramMap.get('vin_number');
    this.is_validvin=true;    
    this.mainService.SearchVin(this.vin_number).subscribe(ret_value => {
      this.isLoading = false;
      this.carInfo = ret_value;
      this.carInfo.vin = this.vin_number;
      this.searched_number = this.vin_number;
    })
  }
  onchange(){
    this.is_validvin = VinValidator.validate(this.vin_number);
    console.log(this.is_validvin);
  }

  search(){    
    if(this.is_validvin){
      this.isLoading=true;
      this.carInfo.id = -1;
      this.mainService.SearchVin(this.vin_number).subscribe(ret_value => {
        this.isLoading = false;
        this.carInfo = ret_value;
        if(ret_value['status'] != 'OK')
          this.carInfo['id'] = -1;
        this.carInfo.vin = this.vin_number;
        this.searched_number = this.vin_number;
      })
    }
    
  }

  GoToPayPage(){
    if(! this.carInfo) return;
    if(this.carInfo.id == -1) return;
    this.mainService.searchedCarInfo = this.carInfo;
    this.router.navigate(['/pay']);
  }
  getLogoUrl() : string {
    let default_url="http://pngimg.com//uploads/bmw_logo/bmw_logo_PNG19707.png";
    if(this.carInfo.carmake && this.carInfo.id != -1){
      default_url = "http://autoszafa.autoszafa.eu/Logos/" + this.carInfo.carmake.toLowerCase() +".png";
    }
    return default_url;
  }
}
