import { Component, OnInit } from '@angular/core';
import {MainService} from '../main.service';
import { CarInfoModel } from 'app/model/carinfo.model';
import { sha256 } from 'js-sha256';
import {environment} from '../../environments/environment';
import { Router } from '@angular/router';
import * as VinValidator from 'vin-validator';

@Component({
  selector: 'app-pay-page',
  templateUrl: './pay-page.component.html',
  styleUrls: ['./pay-page.component.scss']
})
export class PayPageComponent implements OnInit {
  carInfo: CarInfoModel;
  dotpayUrl: string;
  email_addr:string;
  pid= "0m23ouj36noui9mq2jf3jg17aoc2dbun";        
  pin = "m2l5F9b7CNN9RHo9FBm3qikSLms2FsTB";
  amount = 123;
  id = 760691;
  currency = "PLN";
  description = "Pay to Generating Report of Vehicle";   
  urlc = "http://autoszafa.autoszafa.eu:8000/dotpay/callback/?order_id=";
  is_valid_email;
  pay_btn_focus;
  success_url;
  focus;
  constructor(public mainService:MainService, private router:Router ) { 
    this.dotpayUrl = environment['dot_pay_url'];   
  }

  ngOnInit(): void {
    if(!this.mainService.searchedCarInfo){
      this.router.navigate(['home']);
    }
    this.carInfo = this.mainService.searchedCarInfo;
    this.is_valid_email = false;
    this.success_url = environment.success_url;
    
  }
  onEmailChange() : void {
    if (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(this.email_addr))
    {
      this.is_valid_email = true;
    }else{
      this.is_valid_email = false
    }
  }
  getLogoUrl() : string {
    let default_url="http://pngimg.com//uploads/bmw_logo/bmw_logo_PNG19707.png";
    if (! this.carInfo) return default_url;
    if(this.carInfo.carmake && this.carInfo.id != -1){
      default_url = "http://autoszafa.autoszafa.eu/Logos/" + this.carInfo.carmake.toLowerCase() +".png";
    }
    return default_url;
  }
  _generateChk(order_id):string{
    let amount = 123;
    let id = 760691;
    let description = "Pay to Generating Report of Vehicle";
    this.urlc = `http://autoszafa.autoszafa.eu:8000/dotpay/callback/?order_id=${order_id}`;
    this.success_url = environment.success_url + `/?order_id=${order_id}`; 
    return sha256(this.pin + this.id + this.amount + this.currency + this.description + order_id + this.success_url + 4 + this.urlc);
    // return sha256(this.pin +this.pid + order_id); 
  }

  dotPay(): void{

    if(!this.carInfo) return;
    if(!this.is_valid_email) return;
    if(!this.email_addr) return;
    let searched_number = this.carInfo.vin;
    if (VinValidator.validate(searched_number))
    {
      this.mainService.searchedCarInfo = this.carInfo;
      var windowReference = window.open();
      this.mainService.SendReportReuest(searched_number, this.email_addr).subscribe(ret_value =>{
        console.log(ret_value);
        if(ret_value['status'] == 'OK'){
          // dotpay
          // Add RequestId to the Control. CHK
          let order_id = ret_value['order_id'];
          let chk = this._generateChk(order_id);
          let url = this.dotpayUrl + `/?chk=${chk}&id=${this.id}&amount=${this.amount}&currency=${this.currency}&description=${this.description}&control=${order_id}&urlc=${this.urlc}&url=${this.success_url}&type=4`;
          windowReference.location.href = url;
        }
      });
    }    
  }
}
