import { Component, OnInit } from '@angular/core';
import { CarInfoModel } from 'app/model/carinfo.model';
import { MainService } from '../main.service';
import { ActivatedRoute } from '@angular/router';
import {environment} from 'environments/environment';
@Component({
  selector: 'app-get-report-page',
  templateUrl: './get-report-page.component.html',
  styleUrls: ['./get-report-page.component.scss']
})
export class GetReportPageComponent implements OnInit {
  carInfo:CarInfoModel;
  isloading;
  order_id;
  constructor(private mainService:MainService, private route:ActivatedRoute ) { }

  ngOnInit(): void {

    const  order_id = +this.route.snapshot.queryParams['order_id'];
    this.isloading = true;
    console.log(this.isloading);
    console.log(order_id);
    if(order_id){
      this.mainService.GetReport(order_id.toString()).subscribe(ret=> {
        this.isloading = false
        if( ret.status == 'OK'){
          this.order_id = order_id;
          this.carInfo = new CarInfoModel();
          this.carInfo.carmake = ret.carInfo.carmake;
          this.carInfo.id = ret.carInfo.id;
          this.carInfo = ret.carInfo;
        }
      })
    }

  }
  downloadLink(){
    if(this.order_id)
      return environment.api_base_url + "/download_pdf/?order_id=" + this.order_id;
    return "#";
  }

}
