import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders} from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import { environment } from '../environments/environment';
import {CarInfoModel} from './model/carinfo.model';
import { of } from 'rxjs/observable/of';
import { catchError } from 'rxjs/operators';
@Injectable({
  providedIn: 'root'
})
export class MainService {
  
  private api_base_url = environment.api_base_url;
  public display_loading = false;
  public searchedCarInfo;  
  constructor(private http: HttpClient) {         
  }
  
  SearchVin(vin_number:string): Observable<any> {
    return this.http.post<any>(this.api_base_url+"/find/", `vin_number=${vin_number}`, {headers:{
      "Content-Type": "application/x-www-form-urlencoded"
    }}).pipe(
      catchError(this.handleError('SearchVin',new CarInfoModel()))
      );
  }
  SendReportReuest(vin_number:string, email_addr:string) :Observable<any> {
    return this.http.get<any>(this.api_base_url + "/generate_report/?vin=" + vin_number + "&email="+email_addr);
  }
  GetReport(order_id:string):Observable<any>{
    return this.http.post<any>(this.api_base_url + `/generate_report/?order_id=${order_id}`, {});
  }
  ResolveMediaUrl(url):string{
    return environment.asset_base_url + url;
  }

  GetSamplePdfUrl():string{
    return environment.sample_pdf_url;
  }

  PostBlog(formData): Observable<any> {
    return this.http.post(this.api_base_url+"/blogs/", formData);
  }
  ListBlog(data:{limit:number, offset:number}= {limit:3, offset:0}):Observable<any> {
    return this.http.get(this.api_base_url+`/blogs/?limit=${data.limit}&offset=${data.offset}`);
  }
  ListBlogByUrl(data:string):Observable<any> {
    return this.http.get(data);    
  }
  GetBlogById(id):Observable<any> {
    return this.http.get(this.api_base_url+`/blogs/${id}/`)
  }
  PatchBlogById(id, formData): Observable<any> {
    return this.http.patch(this.api_base_url+`/blogs/${id}/`, formData);
  }
  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      console.log("============================")
      // TODO: send the error to remote logging infrastructure
      console.log(error); // log to console instead
      // TODO: better job of transforming error for user consumption
      console.log(`${operation} failed: ${error.message}`);
      console.log(result);
      
      if(operation === 'SearchVin')
      {
        result['id'] = -1;
      }
      // var carinfo = new CarInfoModel();
      // carinfo.caryear="2012";
      // carinfo.carphotos = 8;
      // carinfo.carmake="BMW";
      // carinfo.carmodel = "Series 3";
      // carinfo.carengine = "Diesel";
      // carinfo.carphoto_path="/assets/img/bg1.jpg";
      // result['caryear'] = carinfo.caryear;
      // result['carmake'] = carinfo.carmake;
      // result['carmodel'] = carinfo.carmodel;
      // result['carengine'] = carinfo.carengine;
      // result['carphoto_path'] = carinfo.carphoto_path;
      // result['id'] = 1;
      
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }

}
