import { Component, OnInit, ElementRef, ViewChild, Renderer2 } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import * as VinValidator from 'vin-validator';
import { Validators } from '@angular/forms';
import { MainService } from 'app/main.service';
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  @ViewChild("ellipse") ellipsesvg: ElementRef;
  @ViewChild("circle1") circle1: ElementRef;
  @ViewChild("circle2") circle2: ElementRef;
  @ViewChild("line1") line1: ElementRef;
  @ViewChild("line2") line2: ElementRef;
  constructor(private renderer:Renderer2, private router: Router, public mainService:MainService) { 

  }
  downed:boolean;
  percent:number;
  timerStarted:boolean;
  intervalId:any;
  car1_width:string;
  car2_width:string;
  vin_number:string;
  is_validvin:boolean;
  focus:boolean;
  flag:any;
  blogs:any[];
  offset:number;
  next_url:string;
  is_next_available:boolean;
  isLoading:boolean;

  curve:any = {
    cx: 348,
    cy:106.5,
    rx: 329,
    ry: 87.5
  }
  ngOnInit(): void {
    this.percent = 100;
    this.car1_width = "100%";
    this.car2_width = "0%";
    this.timerStarted = false;
    this.vin_number ="";
    this.is_validvin = false;
    this.blogs = [];
    this.offset = 0;
    this.isLoading = true;
    
    this.renderer.listen('window', 'scroll', (event) => {
      const number = window.scrollY;
      if(number > 2124 || window.pageYOffset > 2124){
          if(!this.timerStarted){
            this.flag = -2;
            this.percent = 100;
            this.timerStarted =true;
            this.intervalId = setInterval(()=>{this.timer_func()}, 10);
          }
        }
    });
    this.mainService.ListBlog({limit:3,offset:0}).subscribe(ret_item => {
      console.log(ret_item);
      this.isLoading = false;
      if(ret_item.next){
        this.is_next_available = true;
        this.next_url = ret_item.next;
      }else{
        this.is_next_available = false;        
      }
      this.blogs = [...this.blogs, ...ret_item.results];
      this.offset = this.offset + ret_item.count;
    })
  }
  loadMore():void {
    this.mainService.ListBlogByUrl(this.next_url).subscribe(ret_item => {
      if(ret_item.next){
        this.is_next_available = true;
        this.next_url = ret_item.next;
      }else{
        this.is_next_available = false;        
      }
      this.blogs = [...this.blogs, ...ret_item.results];
    })
  }
  testclick():void {
    window.open("https://www.google.com", "_blank")
  }
  timer_func():void {
    if(this.percent == 0)
      this.flag = 1;
    if(this.percent >= 40 && this.flag ==1){
      if(this.intervalId) clearInterval(this.intervalId);
    }
    this.percent = this.percent + this.flag;
    this.updateslider();
  }

  ngAfterViewChecked():void{
    if(this.isLoading) return;
    this.updateslider();
  }
  drawline(cx:number, cy:number){
    //677 106.5
    var points1 = (cx-3).toString();
    points1 += " " + (cy-6.5).toString();
    points1 += " " + (cx-9).toString();
    points1 += " " + (cy).toString();
    points1 += " " + (cx-3).toString();
    points1 += " " + (cy+6.5).toString();
    this.line1.nativeElement.setAttribute('points', points1);

    var points2 = (cx+3).toString();
    points2 += " " + (cy-6.5).toString();
    points2 += " " + (cx+9).toString();
    points2 += " " + (cy).toString();
    points2 += " " + (cx+3).toString();
    points2 += " " + (cy+6.5).toString();
    this.line2.nativeElement.setAttribute('points', points2);
    
  }
  updateslider(){
      this.car1_width = this.percent + "%";
      this.car2_width = (100-this.percent) + "%";
      var new_cx = this.curve.rx * this.percent /50 -this.curve.rx;
      var new_cy = Math.sqrt(Math.pow(this.curve.ry,2) * (1- Math.pow(new_cx/this.curve.rx, 2)));
      new_cx = this.curve.cx + new_cx;
      new_cy = this.curve.cy + new_cy;
      this.circle1.nativeElement.setAttribute('cx',new_cx );
      this.circle2.nativeElement.setAttribute('cx',new_cx);
      this.circle1.nativeElement.setAttribute('cy',new_cy );
      this.circle2.nativeElement.setAttribute('cy',new_cy);
      this.drawline(new_cx,new_cy);
  }
  search(){
    if(this.is_validvin){
      this.mainService.display_loading = true;
      this.router.navigate([`/search/${this.vin_number}`]);  
    }
  }
  stripHtml(html)
  {
    var tmp = document.createElement("DIV");
    tmp.innerHTML = html;
    console.log(tmp.textContent);
    return tmp.textContent || tmp.innerText || "";
  }
  onchange(){
    // this.is_validvin =  false;
    this.is_validvin = VinValidator.validate(this.vin_number);
    console.log(this.is_validvin);
  }
  onmouseleave(e){
    if(this.downed == true)
    {
      this.downed = false;
      if(this.percent > 90)
        this.percent = 100;
      if(this.percent<20)
        this.percent = 0;
      this.updateslider();  
    }
  }
  onmousemove(e){
    if(this.downed){
      var boundrect  = this.ellipsesvg.nativeElement.getBoundingClientRect();
      var cur_x = e.clientX;
      if(cur_x < boundrect.x)
        cur_x = boundrect.x;
      if(cur_x > boundrect.x + boundrect.width)
        cur_x = boundrect.x + boundrect.width
      this.percent = (cur_x-boundrect.x)/boundrect.width * 100;
      if(this.percent>99)
        this.percent = 100;
      if(this.percent < 1)
        this.percent = 0;     
      this.updateslider();
      
    }
  }
  onmousedown(e){
    this.downed = true;
  }
  onmouseup(e){
    this.downed = false;
  }
}
