import { NgModule } from '@angular/core';
import { CommonModule, } from '@angular/common';
import { BrowserModule  } from '@angular/platform-browser';
import { Routes, RouterModule } from '@angular/router';

import { ComponentsComponent } from './components/components.component';
import { LandingComponent } from './examples/landing/landing.component';
import { LoginComponent } from './examples/login/login.component';
import { ProfileComponent } from './examples/profile/profile.component';
import { NucleoiconsComponent } from './components/nucleoicons/nucleoicons.component';
import { HomeComponent } from './home/home.component';
import { SearchPageComponent } from './search-page/search-page.component';
import { PayPageComponent } from './pay-page/pay-page.component';
import { GetReportPageComponent } from './get-report-page/get-report-page.component';
import {PostPageComponent} from './post-page/post-page.component';
import { EditBlogPageComponent } from './edit-blog-page/edit-blog-page.component';
const routes: Routes =[
    { path: '', redirectTo: 'home', pathMatch: 'full' },
    { path: 'index',                component: ComponentsComponent },
    { path: 'nucleoicons',          component: NucleoiconsComponent },
    { path: 'examples/landing',     component: LandingComponent },
    { path: 'examples/login',       component: LoginComponent },
    { path: 'examples/profile',     component: ProfileComponent },
    { path: 'home',                 component: HomeComponent },
    { path: 'search/:vin_number',   component: SearchPageComponent },
    { path: 'pay',                  component: PayPageComponent },
    { path: 'get-report',           component: GetReportPageComponent},
    { path: 'post',                 component: PostPageComponent},
    { path: 'blog/:id',             component: EditBlogPageComponent}
];

@NgModule({
    imports: [
        CommonModule,
        BrowserModule,
        RouterModule.forRoot(routes)
    ],
    exports: [
    ],
})
export class AppRoutingModule { }
